              career profile. Click here to complete your profile" type="warning" /> </router-link>
            <br /><br />
            <ckeditor :editor="editor" v-model="application.cover_letter" :config="editorConfig"></ckeditor>
            <!-- <el-form-item label="Cover Letter" prop="cover_letter" :rules="[
              {
                required: true,
