<style scoped>
.el-card {
  margin: 0.2rem;
}

.card-body {
  padding: 0.6rem;
}

.text-muted {
  font-size: 14px;
}

::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

a {
  color: black;
}
</style>
<script>
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import JobService from "../../services/company.service";
export default {
  data() {
    var self = this;
    return {
      editor: ClassicEditor,
      editorConfig: {
        // The configuration of the editor.
      },
      dialog: false,
      loading: false,
      filter: "category",
      filter1: "budget_department",
      period: "month",
      label: "Monthly",
      transactions: [],
      application: [],
      stats: {},
      employee_change: 0,
      employee_count: 0,
      budget_change: 0,
      budget_count: 0,
      expense_change: 0,
      expense_count: 0,
      card_count: 0,
      card_change: 0,
      dashboard: {},
      employee: {},
      jobs: [],
      pagination: { page: 1, size: 10 },
    };
  },
  computed: {
    user() {
      return this.$store.state.auth.user ? this.$store.state.auth.user : {};
    },
  },
  mounted() {
    if (localStorage.getItem('reloaded')) {
      // The page was just reloaded. Clear the value from local storage
      // so that it will reload the next time this page is visited.
      localStorage.removeItem('reloaded');
    } else {
      // Set a flag so that we know not to reload the page twice.
      localStorage.setItem('reloaded', '1');
      location.reload();
    }
    this.getJobs();
    this.getCareerProfile();
    this.getDashboard();
  },
  methods: {
    getCareerProfile() {
      // var profile_id = JSON.parse(localStorage.getItem("profile_id"));
      this.$endpoint.getCareerProfile(this.$store.state.auth.user.id).then(
        (res) => {
          this.loading = false;
          this.employee = res.data.data;
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
            title: "Error",
            message: this.message,
            type: "error",
          });
        }
      );
    },
    getJobs() {
      this.loading = true;
      JobService.getUnappliedJobs({
        page: this.pagination.page,
        size: this.pagination.size,
        search: this.search,
      }).then(
        (res) => {
          this.jobs = res.data;
          this.pagination.meta = res.meta;
          this.loading = false;
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
            title: "Error",
            message: this.message,
            type: "error",
          });
        }
      );
    },
    submit(validate) {
      this.$refs[validate].validate((valid) => {
        if (valid) {
          this.loading = true;
          this.$endpoint
            .applyNow({
              job_id: this.application.job_id,
              cover_letter: this.application.cover_letter,
            })
            .then(
              (res) => {
                this.getJobs();
                this.loading = false;
                this.application = {};
                this.dialog = false;
                this.$notify({
                  title: "Success",
                  message: res.data.message,
                  type: "success",
                });
              },
              (error) => {
                this.loading = false;
                this.message =
                  (error.response &&
                    error.response.data &&
                    error.response.data.message) ||
                  error.message ||
                  error.toString();
                this.$notify({
                  title: "Error",
                  message: this.message,
                  type: "error",
                });
              }
            );
        } else {
          return false;
        }
      });
    },
    getDashboard() {
      this.loading = true;
      this.$endpoint.getDashboard().then(
        (res) => {
          this.loading = false;
          this.dashboard = res.data.data;
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
            title: "Error",
            message: this.message,
            type: "error",
          });
        }
      );
    },
  },
};
</script>
